.footerContents {
    background-color: rgba(255, 255, 255, 0.26);
    border-radius: 50px;
    padding-top: 50px;
    padding-left: 50px;
    padding-right: 50px;
    padding-bottom: 50px;
    margin-bottom: 15px;
}

.footerContents>* {
    text-shadow: 1px 1px 1px black;
}

.footer>hr {
    width: 100vh;
    border: white solid .5px;
    width: 50%;
}

.homeIcon {
    background-image: url('/assets/home.svg');
    background-size: 30px;
    height: 30px;
    width: 30px;
    margin-right: 15px;
}

.phoneIcon {
    background-image: url('/assets/call.svg');
    background-size: 30px;
    height: 30px;
    width: 30px;
    margin-right: 15px;
}